name: 'prolist';
export default {
	props: {
		productList: {
			type: Array,
			default() {
				return [];
			}
		}
	},
	data() {
		return {
			renderImage: false
		};
	},
	onLoad() {},
	methods: {
		getdetial(i) {
			uni.showLoading({
				title: '将前往详情页面',
				duration: 2000,
				mask: true
			});
		}
	}
};
